.modal.TownNews {
    background-color: #8b8370;
    font-family: "Times New Roman", Times, serif;
    text-align: left;
    padding: 30px;
}

.modal.TownNews h3 {
    font-size: 24px;
    font-weight: bold;
    border-bottom: 2px solid #333;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.modal.TownNews h4 {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    margin: 15px 0;
}

.modal.TownNews p {
    font-size: 16px;
    line-height: 1.5;
    margin: 10px 0;
    color: #222;
}

/* Style for important announcements */
.modal.TownNews p:first-of-type {
    font-style: italic;
    border-bottom: 1px dotted #999;
    padding-bottom: 10px;
}

/* Only style the content area, preserve standard modal structure */
.modal-container.modal-TownNews .modal-content {
    background-color: #f4f1e9;
    text-align: left;
    padding: 30px;
    color: #222;
    
}

.modal-container.modal-TownNews .modal-content h3 {
    font-size: 28px;
  font-family: 'Chewy', serif;
    font-weight: bold;
    border-bottom: 2px solid #333;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.modal-container.modal-TownNews .modal-content h4 {
    font-size: 20px;
    font-weight: bold;
    font-family: "Times New Roman", Times, serif;
    color: #333;
    margin: 20px 0 15px 0;
}

.modal-container.modal-TownNews .modal-content p {
    font-size: 16px;
    line-height: 1.6;
    margin: 12px 0;
        font-family: "Times New Roman", Times, serif;

}

/* Add newspaper texture only to content area */
.modal-container.modal-TownNews .modal-content::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/png;base64,');
    opacity: 0.03;
    pointer-events: none;
    z-index: 0;
}
